.container {
    min-height: 100vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background: #ffb33c;
    overflow: hidden;
}

.circle {
    width: 100px;
    height: 100px;
    margin: 0 10px;
    position: relative;
    border-radius: 50%;
    background: #ee3556;
    cursor: pointer;
    box-shadow: 5px -15px 15px rgba(255, 255, 255, 0.05),
    inset -5px -15px 15px rgba(0, 0, 0, 0.3),
    10px 15px 15px rgba(0, 0, 0, 0.3),
    inset -5px 15px 15px rgba(255, 255, 255, 0.05);
    transform-style: preserve-3d;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.circle:before {
    content: '';
    position: absolute;
    width: 10px;
    height: 10px;
    top: 50%;
    left: 50%;
    border-radius: 5px;
    background: rgba(6, 6, 13, 0.55);
    -webkit-transform-origin: 100% 0;
    -moz-transform-origin: 100% 0;
    -o-transform-origin: 100% 0;
    -ms-transform-origin: 100% 0;
    transform-origin: 5px 5px;
    transform:translate(-50%, -50%) rotate(90deg)  ;
}

.rubber {
    position: absolute;
    width: 10px;
    height: 10px;
    top: 50%;
    left: 50%;
    border-radius: 5px;
    background: linear-gradient(#568bdc, #2dcddc,#568bdc);
    z-index: -10;
    -webkit-transform-origin: 100% 0;
    -moz-transform-origin: 100% 0;
    -o-transform-origin: 100% 0;
    -ms-transform-origin: 100% 0;
    transform-origin: 5px 5px;
    transform: translate(-50%, -50%) rotate(90deg) ;
}


.none:before  {
    display: none;
}

